html {
  scroll-behavior: smooth;
}

.react-datepicker-wrapper {
  display: block !important;
}

.swiper {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;

  /* Center slide text vertically */
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.markdownContainer {
  height: 100% !important;
}
.slick-prev:before,
.slick-next:before {
  font-size: 30px !important;
  color: red !important;
}

#teamSwiper .swiper-slide {
  background-color: inherit;
}
