/* Swiper.css */
.swiper-container {
  position: relative;
  background-color: inherit;
}

.swiper-track {
  display: flex;
  transition: transform 0.5s ease;
}

.swiper-slideer {
  flex: 0 0 100%;
  background-color: inherit;
}

.swiper-button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  padding: 10px;
  border: none;
  cursor: pointer;
  font-size: 24px;
}

.swiper-button.prev {
  left: 10px;
  z-index: 1000;
}

.swiper-button.next {
  right: 10px;
  z-index: 1000;
}
